	module.exports = {"data":{"wordpressPost":{"title":"Tobacco industry claims “plain” packs won’t work based on weak evidence","content":"<p>Most studies lack policy relevance; and relevant research lacks key indicators of quality, including peer review</p>\n<p>A critical evaluation of the volume, relevance and quality of evidence submitted by the tobacco industry to oppose standardised packaging of tobacco products doi:10.1136/bmjopen-2013-003757</p>\n<p><a href=\"http://www.bmjopen.bmj.com/content/4/2/e003757.full\">Tobacco companies lack strong, relevant evidence to support their claims that standardised (plain) packaging of tobacco products in the UK won’t work</a>, finds research published in <a href=\"http://www.bmjopen.bmj.com/\">BMJ Open</a>.</p>\n<p>The aim of plain packaging, with no logos, brand imagery, symbols, or promotional text, is to restrict the already limited opportunities that transnational tobacco companies have to market their products, and deter people from starting smoking.</p>\n<p>Australia adopted plain packaging for tobacco products in 2012, the same year that the Department of Health in England held a public consultation on similar plans. The Department then said it wanted to wait for more evidence of the likely impact on tobacco consumption before adopting the policy.</p>\n<p>It has since commissioned an independent review of evidence relating to unbranded and standardised packaging, which is due to report this spring.</p>\n<p>The researchers analysed evidence cited in submissions made to the Department of Health’s consultation on plain packaging by the UK’s four largest transnational tobacco companies: Imperial Tobacco; Japan Tobacco International; Philip Morris Ltd; and British American Tobacco.</p>\n<p>The four companies submitted lengthy consultation responses &#8211; 1521 pages in total, of which 328 comprised their main responses and 1193 provided supplementary material.</p>\n<p>In these submissions, the companies rejected the conclusions of a systematic review, commissioned by the Department of Health, that there was “strong evidence” that plain packaging would reduce the appeal of tobacco products and increase the prominence of health warnings.</p>\n<p>Instead, they argued that there is no evidence that plain packaging would reduce smoking prevalence or deter people from starting to smoke.</p>\n<p>The researchers looked at the volume, relevance (subject matter) and quality (as measured by independence from industry, and peer review) of the evidence cited by the companies and compared it with the evidence from the systematic review.</p>\n<p>Seventy seven out of 143 pieces of evidence were used to promote the companies’ claim that plain packs “won’t work”. Of these, only 17 (22%) addressed standardised packaging, 14 of which were linked to industry. None was published in peer reviewed journals &#8211; a key hallmark of quality.</p>\n<p>Compared with the evidence in the systematic review, relevant evidence cited by the tobacco industry was of significantly lower quality.</p>\n<p>Across all 77 documents, evidence linked to industry was significantly less likely to have been published in a peer reviewed journal than the independent evidence cited by them.</p>\n<p>“With few exceptions, evidence cited by [transnational companies] to promote their claim that standardised packaging ‘won’t work’ lacks either policy relevance or key indicators of quality,” conclude the authors.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"0093bd75-4beb-56eb-909d-61a70e7177e6"}}

